:root {
    box-sizing: border-box;
}

*,
::after,
::before {
    box-sizing: inherit;
}

@font-face {
    font-family: 'Retro Gaming';
    src: url(./fonts/14722.ttf) format('truetype');
}

@keyframes wave {
    0% {
        -webkit-transform: translateY(-3000px);
        transform: translateY(-3000px)
    }

    to {
        -webkit-transform: translateY(100vh);
        transform: translateY(100vh)
    }
}

body {
    font-family: 'Anonymous Pro', monospace;
    font-weight: bold;
    font-size: 1rem;
    color: #d1fb84;
    text-shadow: 0 0 15px #7b9246, 0 0 10px #7b9246;

    background-image: url('img/back.svg');
    background-size: 90vw auto;

    height: 98vh;
    display: flex;
    justify-content: center;
    align-items: center;

}

.crt {
    width: 50rem;
    height: 40rem;
    border-radius: 3%;
    position: relative;
    overflow: hidden;
    padding: 2rem 2rem;
    background: radial-gradient(50% 50% at 50% 50%,#505050  0, #151515 100%);
    box-shadow: inset 0 0 100px 50px rgba(0, 0, 0, .99);
}

.crt__effects:before {
    position: absolute;
    left: 0;
    width: 100%;
    pointer-events: none
}

.crt__effects {
    z-index: -1;
}

.crt__effects:before {
    will-change: transform;
    content: " ";
    height: 20%;
    top: -20%;
    background: linear-gradient(rgba(255, 255, 255, 0),rgba(255, 255, 255, 0.09));
    animation: wave 5s ease-in-out 3s infinite forwards;
}

.glare {
    position: absolute;
    pointer-events: none;
    backface-visibility: hidden;
    background-repeat: none;
    z-index: 3;
}

.glare-1 {
    background: url(./img/glare1.svg);
    background-size: cover;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

.glare-2 {
    left: 0;
    bottom: 0;
    width: 100%;
    height: 180px;
    background: radial-gradient(30% 100% at 100% 100%,hsla(0,0%,100%,0) 0,#fff 0,hsla(0,0%,100%,0) 100%);
    opacity: .12;
}

.crt__content {
    z-index: 6;
}

.noise,
.lines {
    pointer-events: none;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block
}

.noise {
    background: url(./img/noise.png);
    background-blend-mode: overlay;
    opacity: .2;
}

.lines {
    background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, .2) 0), linear-gradient(90deg, rgba(255, 0, 0, .06), rgba(0, 255, 0, .02), rgba(0, 0, 255, .06));
    background-size: 100% 6px, 4px 100%;
    background-position-y: 3px
}

.input-console[type="text"], .input-console[type="text"]:focus {
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    text-shadow: inherit;
    font-weight: inherit;
    color: inherit;
    margin: 0; /* 2 */
    background: transparent;
    border: none;
    outline-width: 0;
}

@media only screen and (min-width: 320px) {
    :root {
        font-size: 1rem;
    }

    .crt {
        height: 24rem;
        padding: 1rem 1rem;
    }

    .input-console-box {
        font-size: .7rem;
    }
}

